.main-wrapper-outer {
  width: 100%;
  min-height: 100%;
  max-width: var(--main-outer-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  overflow: hidden;
}

.main {
  flex-grow: 1;
  flex-shrink: 1;
  padding: 110px 0px 0px 0px;
  @media (max-width: 1300px) {
    padding: 80px 0px 0px 0px;
  }

  body:not(.mp) & {
    padding-bottom: clamp(50px, 5vw, 100px);
  }
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: var(--container-width);
  padding: 0px 20px;
}
