@charset "UTF-8";
:root {
  --container-width: 1330px;
  --main-outer-width: 100%;
  --container-padding: 20px;
  --calculated-padding: calc((100% - var(--container-width)) / 2);
  --theme-color-accent: #219fce;
  --theme-color-hover-accent: #00da95;
  --theme-color-grey: #f8f8f8;
  --theme-color-text-common: #000000;
  --theme-animation-duration: 300ms;
  --font-family: "gothampro", sans-serif;
}

/*
