  cursor: pointer;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
  z-index: 20;
}
.arrow-next img {
  width: 28px;
  height: 28px;
  transform: rotate(-90deg);
  margin: 0px -2px 0px 0px;
}
@media (max-width: 1450px) {
  .arrow-next img {
    width: 12px;
    height: 12px;
  }
}
.arrow-next.swiper-button-lock {
  display: none;
}
.arrow-next.swiper-button-disabled {
  opacity: 0.5;
}
.arrow-next:hover {
  background-color: var(--theme-color-accent);
}
.arrow-next:hover img {
  filter: brightness(0) invert(1);
}
@media (max-width: 1650px) {
  .arrow-next {
    right: -60px;
  }
}
@media (max-width: 1450px) {
  .arrow-next {
    width: 50px;
    height: 50px;
    flex: 0 0 50px;
    right: -15px;
  }
}

