.pagination {
  position: absolute;
  bottom: 30px !important;
  z-index: 10;
  display: flex;
  justify-content: center;
  gap: 3px;
  left: 10px !important;
  width: calc(100% - 20px) !important;
}
@media (max-width: 992px) {
  .pagination {
    position: relative;
    left: 0 !important;
    bottom: 0 !important;
    margin: 17px 10px 0px 10px;
    gap: 5px;
  }
}
.pagination .swiper-pagination-bullet {
  border-radius: 25px;
  background-color: #fff !important;
  flex: 0 1 50px;
  width: 50px;
  height: 3px;
  margin: 0px 0px 0px 0px !important;
  opacity: 1;
}
@media (max-width: 992px) {
  .pagination .swiper-pagination-bullet {
    width: auto;
    background-color: rgba(137, 142, 146, 0.2) !important;
  }
}
.pagination .swiper-pagination-bullet-active {
  background-color: var(--theme-color-accent) !important;
}

.options {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.options__item {
  flex: 0 0 40px;
  position: relative;
  cursor: pointer;
  display: flex;
}
.options__item._active .options__text {
  opacity: 1;
}
.options__input {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
}
.options__input:checked + .options__label .options__icon .circle.active {
  border: 4px solid var(--theme-color-hover-accent);
}
.options__input:checked + .options__label .options__icon.icon-gradient:before {
  opacity: 1;
}
.options__input:checked + .options__label .options__icon.icon-gradient .circle {
  border: none;
  width: 24px;
  height: 24px;
}
.options__label {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  position: relative;
  gap: 4px;
}
.options__icon {
  position: relative;
  border-radius: 50%;
}
.options__icon .circle {
  box-shadow: 0 15px 30px 0 rgba(43, 42, 41, 0.1);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: 4px solid #fff;
  display: block;
  transition: border 0.3s;
  background: var(--theme-color-text-common);
  position: relative;
  overflow: hidden;
  z-index: 3;
}
.options__icon .two-colors {
  position: absolute;
  background: #f5f5f5;
  top: 4px;
  left: 1px;
  width: 50%;
  height: 100%;
  transform: rotate(-30deg);
}
.options__icon.icon-gradient {
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.options__icon.icon-gradient .circle {
  transition: border 0.3s, width 0.3s, height 0.3s;
  border: 1px solid #eaeef1;
  width: 30px;
  height: 30px;
}
.options__icon.icon-gradient::before {
  content: "";
  opacity: 0;
  transition: opacity 0.3s;
  position: absolute;
  top: -1.5px;
  left: -1.5px;
  width: calc(100% + 3px);
  height: calc(100% + 3px);
  border-radius: 50%;
  background-size: 100% 100%;
  background-image: url("../img/bg5.webp");
  z-index: 2;
}
.options__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  color: #898e92;
  display: block;
  margin: 4px 0px 0px 0px;
}

.select {
  min-width: 0;
  position: relative;
}
.select__body {
  position: relative;
}
.select__title {
  color: inherit;
  text-align: left;
  border-radius: 25px;
  background-color: #fff;
  cursor: pointer;
  width: 100%;
  min-height: 40px;
  font-weight: 700;
  font-size: 12px;
  line-height: 133%;
  text-transform: uppercase;
  padding: 5px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.select__title::after {
  content: "";
  transition: transform 0.3s;
  width: 10px;
  height: 10px;
  display: block;
  background-size: 100% 100%;
  background-image: url("../img/icon/arrow1.svg");
}
._select-open .select__title::after {
  content: "";
  transform: rotate(-180deg);
}
.select__value {
  display: flex;
  min-width: 0;
  overflow: hidden;
  align-items: center;
  flex: 1 1 auto;
}
.select__row {
  display: flex;
  align-items: center;
}
.select__content {
  flex: 1 1 auto;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}
.select__price {
  color: var(--theme-color-accent);
  padding: 0px 0px 0px 5px;
}
.select__asset {
  flex: 0 0 62px;
  margin: 0px 20px 0px 0px;
}
.select__asset img {
  width: 62px;
  height: auto;
}
.select__text {
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.select__options {
  position: absolute;
  top: calc(100% + 5px);
  border-radius: 20px;
  min-width: 100%;
  left: 0;
  background-color: #fff;
  border-top: 0;
  border-top: 0;
  padding: 10px 0px 5px 0px;
  max-height: 140px;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.select__options::-webkit-scrollbar {
  display: none;
}
.select__option {
  width: 100%;
  text-align: left;
  cursor: pointer;
  padding: 5px 20px;
  color: inherit;
  font-weight: 700;
  font-size: 12px;
  line-height: 133%;
  text-transform: uppercase;
  transition: color 0.3s;
}
.select__option:hover {
  color: var(--theme-color-accent);
}

.quantity {
  width: 100%;
  height: 60px;
  display: flex;
  border-radius: 30px;
  background-color: #f4f8fb;
  padding: 15px;
}
.quantity__button {
  flex: 0 0 30px;
  height: 30px;
  position: relative;
  cursor: pointer;
  background-color: var(--theme-color-accent);
  border-radius: 50%;
  transition: background-color 0.3s;
}
.quantity__button::before, .quantity__button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transition: all 0.3s ease 0s;
  margin: 0px 0px 0px -7px;
  background-color: #fff;
  width: 14px;
  height: 2px;
}
.quantity__button:hover {
  background-color: var(--theme-color-hover-accent);
}
.quantity__button_plus::before {
  transform: rotate(-90deg);
}
.quantity__input {
  flex: 1 1 auto;
}
.quantity__input input {
  height: 100%;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  text-align: center;
  width: 100%;
  text-align: center;
}

.switch {
  display: flex;
  gap: 15px;
}
.switch input[type=checkbox] {
  display: none;
}
.switch__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 15px;
  cursor: pointer;
  margin: 1px 0px 0px 0px;
}
.switch__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #e7ebee;
  border-radius: 25px;
  transition: background-color 0.3s ease-in-out;
}
.switch__handle {
  position: absolute;
  height: 9px;
  width: 9px;
  left: 3px;
  bottom: 3px;
  background-color: #898e92;
  border-radius: 50%;
  transition: 0.4s;
}
.switch__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  color: #898e92;
}
.switch__text .fw {
  font-weight: 700;
  color: var(--theme-color-accent);
}

.toggle-switch input[type=checkbox]:checked + .switch__background {
  background-color: var(--theme-color-hover-accent);
}

.toggle-switch input[type=checkbox]:checked + .switch__background .switch__handle {
  transform: translateX(15px);
  background-color: #fff;
}

.checkboxes {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.checkbox {
  position: relative;
}
.checkbox__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.checkbox__input:checked + .checkbox__label .checkbox__icon {
  background-color: var(--theme-color-hover-accent);
}
.checkbox__input:checked + .checkbox__label .checkbox__icon:before {
  opacity: 1;
}
.checkbox__icon {
  position: relative;
  border-radius: 4px;
  background-color: #eff3f6;
  transition: background-color 0.3s;
  flex: 0 0 20px;
  height: 20px;
  margin: 3px 0px 0px 0px;
}
.checkbox__icon:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  filter: brightness(0) invert(1);
  background-size: 100% 100%;
  background-image: url("../img/icon/check1.svg");
  transition: opacity 0.3s;
  opacity: 0;
}
.checkbox__label {
  cursor: pointer;
  display: inline-flex;
  position: relative;
  gap: 10px;
}
.checkbox__text {
  font-weight: 900;
  font-size: 18px;
  line-height: 144%;
  text-transform: uppercase;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background: #fff;
  transition: box-shadow 0.3s;
}
.header::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 1px;
  opacity: 0;
  transition: opacity 0.3s;
  background-image: linear-gradient(to right, rgba(137, 142, 146, 0) 0%, rgba(137, 142, 146, 0.504808) 50%, rgba(137, 142, 146, 0) 100%);
}
@media (max-width: 1100px) {
  .header::after {
    display: none;
  }
}
.menu-active .header::after {
  content: "";
  opacity: 1;
}
.header._header-scroll {
  box-shadow: 0 30px 80px 0 rgba(43, 42, 41, 0.1);
}
.header__content .socials {
  font-weight: 900;
  font-size: 14px;
  line-height: 120%;
  text-transform: uppercase;
  text-align: center;
  color: var(--theme-color-accent);
}
.header__content .socials span {
  display: none;
}
@media (max-width: 1100px) {
  .header__content .socials span {
    display: block;
    margin: 0px 10px 0px 0px;
  }
}
@media (max-width: 1100px) {
  .header__content .socials a {
    width: 100%;
    min-height: 50px;
    border-radius: 30px;
    height: auto;
    flex: 0 1 auto;
    background-color: #f4f8fb;
    border: 1px solid transparent;
  }
}
@media (max-width: 1100px) {
  .header__content .socials {
    width: 100%;
    order: 4;
    flex-direction: column;
  }
}
.header__top {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  padding: 32px 0px 36px 0px;
}
@media (max-width: 1330px) {
  .header__top {
    gap: 30px;
    padding: 15px 0px;
