body::after {
  content: "";
  background-color: rgba(0, 0, 0, 0.35);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.8s ease 0s;
  pointer-events: none;
  z-index: 149;
}

.popup-show body::after {
  opacity: 1;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 50px 10px;
  transition: visibility 0.8s ease 0s;
  visibility: hidden;
  pointer-events: none;
}

.popup_show {
  z-index: 150;
  visibility: visible;
  overflow: auto;
  pointer-events: auto;
}

.popup_show .popup__content {
  visibility: visible;
  transform: scale(1);
}

.popup__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
  flex: 1 1 auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup__content {
  position: relative;
  visibility: hidden;
  transform: scale(0);
  transition: transform 0.3s ease 0s;
  width: 100%;
  background: transparent;
  max-width: 410px;
  box-shadow: none;
}
.lock .popup__content {
  visibility: visible;
}

.popup__close {
  cursor: pointer;
  position: absolute;
  top: 18px;
  right: 10px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 12;
}
.popup__close img {
  width: 30px;
  height: 30px;
}

.popup__body {
  position: relative;
  z-index: 10;
}
.popup__body .tabs__navigation {
  display: flex;
  justify-content: center;
  margin: 0px 0px 0px 0px;
  padding: 0px 70px;
  gap: 50px;
}
.popup__body .tabs__navigation::after {
  content: "";
  display: none;
}
@media (max-width: 480px) {
  .popup__body .tabs__navigation {
    padding: 0px 25px;
    gap: 30px;
  }
}
@media (max-width: 359px) {
  .popup__body .tabs__navigation {
    gap: 15px;
  }
}
.popup__body .tabs__title {
  width: auto;
  font-weight: 900;
  font-size: 22px;
  line-height: 118%;
  text-transform: uppercase;
  text-align: center;
  color: #898e92;
  padding: 63px 0px 26px 0px;
  transition: color 0.3s;
  background-color: transparent;
  border: none;
  margin: 0px 0px 0px 0px;
}
.popup__body .tabs__title:hover {
  color: var(--theme-color-text-common);
}
.popup__body .tabs__title._tab-active {
  color: var(--theme-color-text-common);
}
.popup__body .tabs__title::before {
  content: "";
  display: block;
  bottom: 0;
}
.popup__body .tabs__title::after {
  content: "";
  display: none;
}
